.calendarCenter{
    width: 100%;
    text-align: center;
}
.calendarContainer{    
    text-align: center;
    background-color: rgb(204, 207, 230);
    padding: 20px;
    border-radius: 5px;
}
.calendar{
    --height:120px;
    width: var(calc(--height * 7));
    width: 850px;
    
    border-left: 1px solid blue;
    border-bottom: 1px solid blue;
}
.calendarDay{
    height: var(--height);
    width: var(--height);
    border-top: 1px solid blue;
    border-right: 1px solid blue;
    overflow-y: auto;
}
.calendarDay:hover{
    background-color: rgb(195, 201, 248);
}
.title{
    width: 100%;
    font-size: 30px;
    text-align: center;
}
.dayDisplay{
    user-select: none;
    font-size: 12px;
}
.eventMenu{
    position: absolute;
    height: 440px;
    width: 760px;
    top: 40px;
    left: calc(50vw - 400px);
}
.contactDiv{
    height: 95%;
    width: 50%;
    border:1px solid blue;
    border-radius: 5px;
    float: left;
    margin:10px ;
    box-sizing: border-box;
    padding: 10px;
}
#eventContactImage{
    width: 100%;
    object-fit: contain;    
}
.contactDiv textarea{
    height: 110px;
    min-width: 100%;
    max-width: 100%;
    box-sizing: border-box;
    padding-top: 5px;
}
.eventMenuData{
    width: 45%;
    float: left;
    margin-top: 10px;    
}
.eventMenuData textarea{
    height: 260px;
    max-width: 100%;
    min-width: 100%;
    box-sizing: border-box;
    padding-top: 5px;
}
.width90{
    width: 90%;
    float: left;
}
.eventContactImg{
    height: 200px;
    width: 200px;
}
.bottomRow{    
    width: 56%;  
    position: absolute;
    bottom:10px;
    right: -70px;
    display: flex;
    justify-content: space-between;
}
.event{
    border-radius: 3px;
    border:1px solid blue;
    background-color: rgb(185, 185, 255);
    cursor: pointer;
    margin: 2px 5px     2px 5px;
    padding: 2px 5px 2px 5px;
    font-size: 11px;
    
}
.event:hover{
    background-color: rgb(140, 140, 255);
}
.eventNone{
    border:1px solid rgb(163, 163, 163);
    background-color: rgb(204, 207, 230);
}
.eventNone:hover{    
    background-color: rgb(207, 207, 207);
}
.eventGray{
    border:1px solid gray;
    background-color: rgb(207, 207, 207);
}
.eventGray:hover{    
    background-color: rgb(161, 161, 161);
}
.eventRed{
    border:1px solid red;
    background-color: rgb(252, 172, 172);
}
.eventRed:hover{
    background-color: rgb(255, 124, 124);
}
.eventOrange{
    border:1px solid orange;
    background-color: rgb(253, 227, 179);
}
.eventOrange:hover{    
    background-color: rgb(255, 205, 113);
}
.eventYellow{
    border:1px solid yellow;
    background-color: rgb(253, 253, 197);
}
.eventYellow:hover{    
    background-color: rgb(255, 255, 151);
}
.eventGreen{
    border:1px solid green;
    background-color: rgb(158, 199, 158);
}
.eventGreen:hover{    
    background-color: rgb(102, 196, 102);
}
.eventBlue{
    border:1px solid blue;
    background-color: rgb(179, 179, 255);
}
.eventBlue:hover{    
    background-color: rgb(109, 109, 255);
}
.eventPurple{
    border:1px solid purple;
    background-color: rgb(207, 154, 207);
}
.eventPurple:hover{    
    background-color: rgb(204, 104, 204);
}

.event .hoverMenu{
    display: none;
}

.event:hover .hoverMenu{
    
    display: inline-block;
    position: absolute;
    
}
.eventHoverMenu{
    height: 200px;
    width: 200px;
}